import React, { createContext, useContext, useState, useEffect } from 'react';
import { getFirestore, collection, doc, getDocs, getDoc } from 'firebase/firestore';
import { useAuth } from './AuthContext';

const LearningContext = createContext();

export const useLearning = () => {
  return useContext(LearningContext);
};

export const LearningProvider = ({ children }) => {
  const { user } = useAuth();
  const [courses, setCourses] = useState([]);
  const [selectedCourse, setSelectedCourse] = useState(null);
  const [nextLesson, setNextLesson] = useState(null);
  const [nextWorkbook, setNextWorkbook] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const db = getFirestore();

  useEffect(() => {
    const fetchCourses = async () => {
      try {
        setLoading(true);
        const coursesSnapshot = await getDocs(collection(db, 'courses'));
        const coursesData = coursesSnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
        }));
        setCourses(coursesData);

        if (user && user.courseID) {
          const courseDocRef = doc(db, 'courses', user.courseID);
          const courseDoc = await getDoc(courseDocRef);

          if (courseDoc.exists()) {
            const courseData = { id: courseDoc.id, ...courseDoc.data() };

            const modulesSnapshot = await getDocs(collection(courseDocRef, 'modules'));
            const modulesData = modulesSnapshot.docs.map(doc => ({
              id: doc.id,
              ...doc.data(),
            }));

            let allLessons = [];
            let allWorkbooks = [];

            for (const module of modulesData) {
              const lessonsSnapshot = await getDocs(collection(courseDocRef, 'modules', module.id, 'lessons'));
              const lessonsData = lessonsSnapshot.docs.map(doc => ({
                id: doc.id,
                moduleID: module.id,
                ...doc.data(),
              }));
              allLessons = allLessons.concat(lessonsData);

              const workbooksSnapshot = await getDocs(collection(courseDocRef, 'modules', module.id, 'workbooks'));
              const workbooksData = workbooksSnapshot.docs.map(doc => ({
                id: doc.id,
                moduleID: module.id,
                ...doc.data(),
              }));
              allWorkbooks = allWorkbooks.concat(workbooksData);
            }

            // Filter out finished lessons and workbooks
            const unfinishedLessons = allLessons.filter(lesson => !lesson.finished);
            const unfinishedWorkbooks = allWorkbooks.filter(workbook => !workbook.finished);

            setNextLesson(unfinishedLessons[0] || null);
            setNextWorkbook(unfinishedWorkbooks[0] || null);

            setSelectedCourse({
              ...courseData,
              modules: modulesData,
              lessons: allLessons,
              workbooks: allWorkbooks,
            });
          }
        }
      } catch (err) {
        setError('Failed to fetch course data');
        console.error('Error fetching course data:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchCourses();
  }, [user, db]);

  return (
    <LearningContext.Provider
      value={{
        courses,
        selectedCourse,
        nextLesson,
        nextWorkbook,
        loading,
        error,
      }}
    >
      {!loading && children}
    </LearningContext.Provider>
  );
};
