import React, { createContext, useContext, useEffect, useState } from 'react';
import { getAuth, onAuthStateChanged, signInWithEmailAndPassword, signOut } from 'firebase/auth';
import { getFirestore, doc, getDoc } from 'firebase/firestore';
import debounce from 'lodash/debounce';

const AuthContext = createContext();

export const useAuth = () => {
  return useContext(AuthContext);
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(() => {
    const storedUser = sessionStorage.getItem('user');
    return storedUser ? JSON.parse(storedUser) : null;
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const auth = getAuth();
  const db = getFirestore();

  useEffect(() => {
    const fetchUserData = async (currentUser) => {
      try {
        if (!currentUser) {
          setUser(null);
          sessionStorage.removeItem('user');
          setLoading(false);
          return;
        }

        // Fetch user data from Firestore
        const userRef = doc(db, 'users', currentUser.uid);
        const userDoc = await getDoc(userRef);

        if (userDoc.exists()) {
          const userData = {
            uid: currentUser.uid,
            email: currentUser.email,
            ...userDoc.data(), // Spread additional user data from Firestore
          };

          // Cache user data in sessionStorage
          sessionStorage.setItem('user', JSON.stringify(userData));

          setUser(userData);
        } else {
          console.log("No user data found in Firestore for:", currentUser.uid);
          setUser({
            uid: currentUser.uid,
            email: currentUser.email,
          });
        }
      } catch (error) {
        console.error('Error fetching user data from Firestore:', error);
        setError('Failed to load user data.');
      } finally {
        setLoading(false);
      }
    };

    const debouncedFetch = debounce(fetchUserData, 300);

    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      debouncedFetch(currentUser);
    });

    return () => {
      unsubscribe();
      debouncedFetch.cancel();
    };
  }, [auth, db]);

  const login = async (email, password) => {
    setLoading(true);
    try {
      await signInWithEmailAndPassword(auth, email, password);
    } catch (error) {
      console.error('Login error:', error);
      setError('Failed to log in.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  const logout = async () => {
    setLoading(true);
    try {
      await signOut(auth);
      setUser(null);
      sessionStorage.removeItem('user');
    } catch (error) {
      console.error('Logout error:', error);
      setError('Failed to log out.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  const value = {
    user,
    login,
    logout,
    loading,
    error,
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
};
